<template>

    <div class="footer-wrapper color-fff">
        <div class="container">
            <div class="row">
                <div class="col-md-3">
                    <div>
                        <img class="logo" src="../assets/images/logo.png" alt="">
                    </div>
                </div>
                <div class="col-md-9">
                    <div>
                        {{$t('title')}}
                    </div>
                    <div v-html="$t('declaration')"></div>
                </div>
            </div>
            <hr/>
            <div class="row">
                <div class="col-md-6 text-left">
                    <div class="c-footer-dropdown">
                        <b-dropdown dropup variant="light" class="m-2">
                            <template slot="button-content" class="color-fff">
                                <img :src="$t('langPng')" :alt="$t('langName')">
                                <span class="color-fff">
                                    {{$t('langName')}}
                                    <i class="fa fa-angle-up"></i>
                                </span>
                            </template>
                            <b-dropdown-item @click="changeLang('zh-CN')">
                                <img src="../assets/images/zh-CN.png" alt="简体中文" />
                                简体中文
                            </b-dropdown-item>
                            <b-dropdown-item @click="changeLang('en')">
                                <img src="../assets/images/en.png" alt="English" />
                                English
                            </b-dropdown-item>
                        </b-dropdown>
                    </div>
                </div>
                <div class="col-md-6 text-right lh-52">
                    <div>
                        &copy; 2018 lq186.com
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
export default {
  name: 'Footer',
  methods: {
    changeLang: function (lang) {
      this.$i18n.locale = lang
      document.title = this.$t('title')
    }
  }
}
</script>

<style scoped>

    .logo {
        width: 5rem;
        height: 5rem;
    }

    hr {
        border-color: rgba(255, 255, 255, .5);
    }

    .footer-wrapper {
        padding: 1rem;
        background-color: #001f3f;
    }
    .color-fff {
        color: #ffffff !important;
        text-decoration: none
    }
    .container {
        margin-top: 1rem;
    }
    .lh-52 {
        line-height: 52px;
    }

    .col-md-3 div, .col-md-9 div {
        margin: 1rem;
    }
</style>



