<template>
  <div class="c-nav">
    <b-navbar toggleable="md" type="dark" variant="dark" fixed="top">

    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

    <b-navbar-brand href="#/">
      <img class="logo" src="../assets/images/logo.png" alt="">
      <span class="brand">{{$t("title")}}</span>
    </b-navbar-brand>

    <b-collapse is-nav id="nav_collapse">

      <b-navbar-nav>
        <li class="nav-item">
          <router-link to="/java" class="nav-bg nav-link">
            {{$t("Java")}}
          </router-link>
        </li>
        <li class="nav-item">
          <router-link to="/go" class="nav-bg nav-link">
            {{$t("Go")}}
          </router-link>
        </li>
        <li class="nav-item">
          <router-link to="/nodejs" class="nav-bg nav-link">
            {{$t("NodeJs")}}
          </router-link>
        </li>
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <li class="nav-item">
          <router-link to="/login" class="nav-link">
            {{$t("登录")}}
          </router-link>
        </li>

        <b-nav-item-dropdown right>
          <template slot="button-content">
            <img :src="$t('langPng')" :alt="$t('langName')">
            <i class="fa fa-angle-down"></i>
          </template>
          <b-dropdown-item @click="changeLang('zh-CN')">
            <img src="../assets/images/zh-CN.png" alt="简体中文" />
            简体中文
          </b-dropdown-item>
          <b-dropdown-item @click="changeLang('en')">
            <img src="../assets/images/en.png" alt="English" />
            English
          </b-dropdown-item>
        </b-nav-item-dropdown>

      </b-navbar-nav>

    </b-collapse>
  </b-navbar>
</div>
</template>

<script>
export default {
  name: 'Nav',
  methods: {
    changeLang: function (lang) {
      this.$i18n.locale = lang
      document.title = this.$t('title')
    }
  },
  mounted () {
    document.title = this.$t('title')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .logo {
    width: 2rem;
    height: 2rem;
  }
  .brand {
    font-size: 80%;
  }
</style>



